.call-dialog {
  min-width: 320px;
  max-width: 440px;
  min-height: 676px;
  max-height: 100vh;
  padding: 0;
  position: fixed;
  top: 0;
  margin: 0;
  left: inherit;
  border: none;
  box-sizing: border-box;
  box-shadow: 16px 0px 52px 0px #0E185033;
  z-index: 2;
  -webkit-animation: showUpDialog 0.3s ease-in-out;
          animation: showUpDialog 0.3s ease-in-out; }

.call-dialog__inner {
  min-width: 320px;
  max-width: 440px;
  min-height: 676px;
  max-height: 100vh;
  padding-left: 4%;
  padding-right: 4%;
  padding-top: 20px; }

.call__title {
  font-family: 'TTLakesBold';
  padding-top: 25px;
  margin-top: 0; }

.call__title::before {
  content: '';
  display: block;
  position: absolute;
  height: 40px;
  width: 4px;
  left: 0;
  top: 85px;
  background-color: #41F6D7;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px; }

.form__input {
  height: 48px;
  width: 92%;
  box-sizing: border-box;
  border: 1px solid #EAEAEA;
  border-radius: 5px;
  color: #B5B6BC;
  font-family: 'TTLakesMedium';
  font-size: 16px;
  margin-bottom: 20px;
  padding-left: 20px; }

.form__info {
  font-family: 'TTLakesMedium';
  font-size: 14px;
  color: #7E7E82;
  margin-top: 0;
  margin-bottom: 20px; }

.form__link {
  color: #FF3E79;
  text-decoration: none; }

.form__link:hover,
.form__link:focus {
  text-decoration: underline; }

#callClose {
  transition: 0.3s; }

#callClose:hover,
#callClose:focus {
  cursor: pointer;
  transform: scale(1.1); }

.call-dialog .form__button {
  font-family: 'TTLakesBold';
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #ff3e79;
  height: 32px;
  width: 136px;
  border-radius: 25px;
  right: -10px;
  padding-right: 15px;
  transition: 0.2s; }

.form__button:hover,
.form__button:focus {
  cursor: pointer;
  transform: scale(1.1); }

.call-dialog .form__button::after {
  content: '';
  background-image: url(./static/goside.svg);
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
  right: 5px;
  top: 4px; }

#formCall > .form__button {
  display: block;
  position: absolute;
  left: inherit;
  margin-right: 7%; }

.call-dialog::-webkit-backdrop {
  background-color: #fff;
  opacity: 96.05%; }

.call-dialog::backdrop {
  background-color: #fff;
  opacity: 96.05%; }

@-webkit-keyframes showUpDialog {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

@keyframes showUpDialog {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

@media (min-width: 768px) {
  #callClose {
    position: fixed;
    top: 40px;
    right: 470px; }
  .call-dialog {
    height: 940px;
    max-height: 100vh; }
  .call__title::before {
    content: '';
    display: block;
    position: absolute;
    height: 40px;
    width: 4px;
    left: 0;
    top: 40px;
    background-color: #41F6D7;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px; } }

@media (max-width: 767px) {
  .call-dialog {
    height: 940px;
    max-height: 100vh; } }
